
.switch {
    position: relative;
    display: inline-block;
    width: 79px;
    height: 34px;
}

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;

    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border-radius: 22px;
    background-color: lightgray;

}

.slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    border-radius: 50%;
    left: 4px;
    bottom: 4px;
    background-color: white;
    transition: 0.5s;
}

input:checked + .slider:before {
    transform: translateX(45px);
}

input:checked + .slider {
    background-color: #4800ce;
}

